export default "<template>\r\n    <div class=\"home\">\r\n      <raw-loader :mytxt=\"require('!raw-loader!./TestHasClaim.vue').default\"></raw-loader>\r\n      <h1>Get permission</h1>\r\n      <b-row>\r\n        <b-col md=\"4\" class=\"bg-light\">\r\n            <b-img src=\"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQsVEPDG0sS_w7wHX12oZPqjxSXJ2Y5f2VTig&usqp=CAU\" fluid alt=\"Fluid image\"></b-img>\r\n        </b-col>\r\n        <b-col md=\"8\">\r\n            <ul>\r\n                <li v-for=\"(v,k,i) in ds\" :key=\"i\">\r\n                    <input type=\"checkbox\" v-model=\"ds[k]\" />\r\n                    {{ k }} \r\n                </li>\r\n            </ul>\r\n        </b-col>\r\n      </b-row>\r\n    </div>\r\n  </template>\r\n  <script>\r\nimport fetchClient from '@/fetchClient'\r\nimport TumLum from '@/TumLum';\r\n  export default {\r\n    components: {\r\n    },\r\n    data(){\r\n        return{\r\n            ds:[]\r\n        }\r\n    },\r\n    mounted(){\r\n        this.TestHasClaim();\r\n    },\r\n    methods: {\r\n        async TestHasClaim(){\r\n            return await fetchClient.get(`/api/Permission/TestHasClaim`)\r\n            .then(res=>{\r\n                this.ds=res.data;\r\n                //alert(JSON.stringify(res));\r\n                /*{\"data\":{\r\n                    \"them.hs\":false,\r\n                    \"xoa.hs\":true,\r\n                    \"sua.hs\":false,\r\n                    \"xem.hs\":false,\"them.ban\":false,\"them.mon\":false,\"xoa.mon\":false},\"status\":200,\"statusText\":\"OK\",\"headers\":{\"conte\r\n                */\r\n                    \r\n\r\n\r\n            }).catch(err=>{\r\n                TumLum.XuLyErr(err);\r\n            })\r\n        }\r\n    }\r\n  }\r\n  </script>\r\n  ";